import styled from 'styled-components'

const Button = styled.button`
    background-color: #232323;
    color: white;
    border-radius: 6px;
    padding: 16px 32px;
    font-size: 16px;
    border: 1px solid #232323;
    cursor: pointer;`
;

export default Button


