import React from 'react'

const LogoText = (props) => {

    const styles = {
    fontWeight: 'bold',
    color: '#232323',
    fontSize: props.fontSize
}
    return (
        <div style={styles}>
            Afriexporter
        </div>
    )
}

export default LogoText





